.all {
  padding-top: 16px;
  background-color: $c-black;
  color: $c-lightGray;
  overflow-y: scroll;
}
.category {
  margin-top: 20px;
  text-align: center;

  &__title {
    font-size: 34px;
    margin: 50px 0;
    color: $c-lightGreen;
  }

  &__products {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 5px;
   
  @include onTablet {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }

  @include onDesktop {
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
  }
}