.cart {
  padding-top: 16px;
  background-color: $c-black;
  color: $c-lightGray;
  overflow-y: scroll;

  &__title {
    margin: 0 auto 30px;
    width: 100px;
    color: $c-white;
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
  }

  &__order {
    margin-top: 20px;
  }

  &__button {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    text-decoration: none;
    color: #fff;
    background-color: #ff0000;
    border: none;
    border-radius: 4px;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: darken(#ff0000, 10%);
    }
  }
}
.delete-button {
  margin-top: 5px;
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  text-decoration: none;
  color: #fff;
  background-color: $c-darkGray;
  border: none;
  border-radius: 4px;
  transition: background-color 0.3s ease;

  &:hover {
    background-color: darken($c-darkGray2, 10%);
  }
}

.img {
  width: 150px;
  height: 180px;

  @include onTablet {
    width: 200px;
    height: 230px;
  }

  @include onDesktop {
    width: 375px;
    height: 500px;
  }
}

.product-item__title {
  max-width: 160px;

  @include onTablet {
    max-width: 200px;
  }

  @include onDesktop {
    max-width: 400px;
  }
}